.pagination{
  display: flex;
  align-items: center;
  margin-left: -.25em;
  li:not(.prev):not(.next){
    margin: 0 .25em;
    a{
      height: 2em;
      min-width: 2em;
      padding: 0 .5em;
      border-radius: 3.125em;
      @include center;
      font-size: 1em;
      line-height: 120%;
    }
    &.active{
      pointer-events: none;
    }
    &:hover,
    &.active{
      a{
        color: $button-main;
        background-color: $grey02;
      }
    }
  }
  .active{

  }
  a{
    @include center;
    width: 100%;
    height: 100%;
  }
  .next,
  .prev{
    width: 2em;
    height: 2em;
    @include center;
    margin: 0 .625em;
    a{
      @include bgImgSize(100%);
    }
  }
  .prev{
    a{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='33' height='33' rx='16.5' stroke='%23EB1F42'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18.8536 13.6464C19.0488 13.8417 19.0488 14.1583 18.8536 14.3536L16.2071 17L18.8536 19.6464C19.0488 19.8417 19.0488 20.1583 18.8536 20.3536C18.6583 20.5488 18.3417 20.5488 18.1464 20.3536L15.1464 17.3536C14.9512 17.1583 14.9512 16.8417 15.1464 16.6464L18.1464 13.6464C18.3417 13.4512 18.6583 13.4512 18.8536 13.6464Z' fill='%23EB1F42'/%3E%3C/svg%3E");
    }
    &.disabled{
      a{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='33' height='33' rx='16.5' stroke='%23D3D3D3'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18.8536 13.6464C19.0488 13.8417 19.0488 14.1583 18.8536 14.3536L16.2071 17L18.8536 19.6464C19.0488 19.8417 19.0488 20.1583 18.8536 20.3536C18.6583 20.5488 18.3417 20.5488 18.1464 20.3536L15.1464 17.3536C14.9512 17.1583 14.9512 16.8417 15.1464 16.6464L18.1464 13.6464C18.3417 13.4512 18.6583 13.4512 18.8536 13.6464Z' fill='%23D3D3D3'/%3E%3C/svg%3E");
      }
    }
  }
  .next{
    a{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='33' height='33' rx='16.5' stroke='%23EB1F42'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.1464 13.6464C15.3417 13.4512 15.6583 13.4512 15.8536 13.6464L18.8536 16.6464C19.0488 16.8417 19.0488 17.1583 18.8536 17.3536L15.8536 20.3536C15.6583 20.5488 15.3417 20.5488 15.1464 20.3536C14.9512 20.1583 14.9512 19.8417 15.1464 19.6464L17.7929 17L15.1464 14.3536C14.9512 14.1583 14.9512 13.8417 15.1464 13.6464Z' fill='%23EB1F42'/%3E%3C/svg%3E");
    }
    &.disabled{
      a{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='33' height='33' rx='16.5' stroke='%23D3D3D3'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.1464 13.6464C15.3417 13.4512 15.6583 13.4512 15.8536 13.6464L18.8536 16.6464C19.0488 16.8417 19.0488 17.1583 18.8536 17.3536L15.8536 20.3536C15.6583 20.5488 15.3417 20.5488 15.1464 20.3536C14.9512 20.1583 14.9512 19.8417 15.1464 19.6464L17.7929 17L15.1464 14.3536C14.9512 14.1583 14.9512 13.8417 15.1464 13.6464Z' fill='%23D3D3D3'/%3E%3C/svg%3E");
      }
    }
  }
}

.page-nav{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
  .pagination{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .page-nav{
    &__btn{
      width: 100%;
    }
  }
}


