.patrons-stat{
  margin-top: 3em;
  padding-bottom: 1.75em;
  position: relative;
  &__items{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6));
  }
  &__start{
    position: absolute;
    top: 1.5em;
    left: 0;
    margin-top: .5em;
    line-height: 120%;
    color: $grey07;
  }
  &__item{
    position: relative;
    &-fill{
      height: 1.5em;
      border-radius: 6.25em;
      background-color: $primary;
    }
    &-num{
      position: absolute;
      right: 0;
      top: 100%;
      margin-top: .5em;
      transform: translateX(50%);
      font-size: 1em;
      line-height: 120%;
      color: $grey07;
    }
    &:last-child{
      .patrons-stat__item-num{
        transform: unset;
        margin-top: .25em;
        color: $grey06;
        span{
          font-size: 1.5em;
        }
      }
    }
  }
}

.patrons-list{
  margin-top: 5em;
  @include grid100gap(4em);
}

.patrons-group{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5em));
  gap: 1em 3em;
  position: relative;
  &__line{
    position: absolute;
    top: -2em;
    left: 50%;
    transform: translateX(-50%);
    width: 1em;
    height: calc(100% + 4em);
    border-radius: 6.25em;
  }
  @media screen and (min-width: 993px){
    &:first-child{
      .patrons-group__line{
        height: calc(100% + 3em);
        top: -1em;
      }
    }
  }
}

.patron-card{
  padding: 2em;
  background-color: $primary-tertiary;
  &__img{
    @include imgCover(100%);
    background-color: $white;
    background-image: url(../img/avatar.svg);
    background-size: cover;
    background-position: center;
    &-wrap{
      max-width: 8.75em;
      margin-top: 1.25em;
    }
  }
  &__type{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
    text-transform: uppercase;
    color: $primary;
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
  &__desc{
    margin-top: 1.25em;
  }
}

.patron-card2{
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: 15.8125em calc(100% - 15.8125em - 1.625em);
  gap: 1.625em;
  .patron-card__img-wrap{
    max-width: 100%;
    margin-top: .5em;
  }
  &__quote{
    padding: .75em 0;
    border-bottom: $border;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 100%;
      width: .3125em;
      height: .3125em;
      border-radius: 50%;
      transform: translate(-50%,-50%);
      background-color: $primary;
    }
    img{
      width: 1.625em;
    }
  }
  &__text{
    margin-top: 1.125em;
  }
}

.patron-card3{
  padding: .8125em 1.5em;
  .patron-card__type{
    margin-top: .25em;
  }
}

.patrons-list2{
  margin-top: 3em;
}

.patron-card4{
  padding: 2em 2.5em;
  border: $border4;
  &__img{
    @include imgCover(100%);
    overflow: unset;
    &-wrap{
      margin-bottom: 3.75em;
    }
  }
  &__title{
    min-height: 2.4em;
  }
}


@media screen and (max-width: 992px){
  .patrons-stat{
    padding-top: 1.75em;
    margin-top: 1.5em;
    &__start{
      font-size: .875em;
      top: 3.86em;
      margin-top: 0;
    }
    &__item{
      &:nth-child(odd){
        .patrons-stat__item-num{
          top: unset;
          bottom: 100%;
          margin: 0;
        }
      }
      &:last-child{
        .patrons-stat__item-num{
          margin-top: .14em;
          span{
            font-size: 1em;
          }
        }
      }
      &-num{
        margin-top: .14em;
        font-size: .875em;
        white-space: nowrap;
      }
    }
  }

  .patrons-list{
    margin-top: 1.5em;
    gap: 2em;
  }

  .patrons-group{
    grid-template-columns: 100%;
    padding-left: 2em;
    &__line{
      top: -1em;
      left: 0;
      transform: unset;
      height: calc(100% + 2em);
    }
    &:first-child{
      .patrons-group__line{
        height: calc(100% + 1.5em);
        top: -.5em;
      }
    }
  }

  .patrons-list2{
    margin-top: 1.5em;
  }

  .patron-card4{
    padding: 1em;
  }
}


@media screen and (max-width: 576px){
  .patrons-group{
    gap: .75em;
    &__items{
      gap: .75em;
    }
  }

  .patron-card{
    padding: 1em;
    text-align: center;
    &__img{
      &-wrap{
        margin-left: auto;
        margin-right: auto;
        margin-top: 1em;
      }
    }
    &__type{
      justify-content: center;
    }
  }

  .patron-card2{
    grid-template-columns: 100%;
    gap: 1em;
    .patron-card__img-wrap{
      max-width: 14.5em;
    }
    .patron-card__type,
    .patron-card__desc{
      margin-top: .5em;
    }
    &__quote,
    &__text{
      text-align: left;
    }
  }

  .patron-card3{
    text-align: left;
    .patron-card__type{
      justify-content: unset;
    }
  }

  .patrons-list2{
    gap: .75em;
  }

  .patron-card4{
    &__img-wrap{
      padding: 1.625em 0;
    }
    &__title{
      min-height: unset;
    }
  }
}
