.deposit-group{
  &._self-pay{
    display: none;
  }
  &__content{
    margin-top: 1.5em;
    padding: 2em;
    border: $border2;
  }
}

.pay-ways{
  margin-top: 1em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
}

.pay-way{
  label{
    transition: all .3s;
    height: 4.1875em;
    @include center;
    border: 1px solid $primary;
    svg{
      margin-right: .625em;
      max-width: 2.3125em;
      max-height: 1.1875em;
    }
  }
  input:hover~label,
  input:checked~label{
    background-color: $primary;
    color: $white;
    path:not(._not-fill){
      fill: $white;
    }
    ._stroke{
      fill: none !important;
      stroke: $white;
    }
  }
  &:not(:last-child){
    @media screen and (min-width: 993px){
      label{
        border-right: none;
      }
    }
  }
}

.payer-info{
  &__anonymous{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
    .m-switch{
      margin-right: .5em;
    }
  }
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 20.3125em calc(100% - 21.3125em);
    gap: .75em 1em;
  }
  &__col{
    padding: 1.375em 1em 1.375em 2em;
    border: $border2;
  }
  &__col2{
    padding: 2em;
    border: $border2;
  }
  &__status{
    @include grid100gap(2em);
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
}

.pay-target{
  &__type{
    display: none;
    &._active{
      display: block;
    }
  }
  .switch-pair{
    margin-bottom: 2em;
  }
}




@media screen and (max-width: 992px){
  .pay-ways{
    grid-template-columns: 100%;
  }

  .pay-way{
    &:not(:last-child){
      label{
        border-bottom: none;
      }
    }
  }

  .payer-info{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 1.375em 1em;
    }
    &__col2{
      padding: 1em;
    }
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .75em));
    }
  }

  .deposit-group{
    &__content{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .payer-info{
    &__fields{
      grid-template-columns: 100%;
    }
  }
}
