.fg {
  position: relative;
  margin: 0;
  display: flex;
  flex-direction: column;
  label{
    order: -1;
    display: block;
    font-size: 1em;
    line-height: 120%;
    margin-bottom: .25em;
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0;
    border: 1px solid $grey04;
    padding: 0 .75em;
    height: 2.75em;
    width: 100%;
    font-size: 1em;
    line-height: 120%;
    background-color: $white;
    color: #000;
    &::placeholder {
      color: $grey05;
    }
    &:focus,
    &:hover {
      border-color: $primary;
    }
    &:disabled{
      border-color: $grey03;
      color: $grey04;
      &~label{
        color: $grey04;
      }
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.6875em;
    padding: 0 0 0 1.25em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:focus{
      border: 1px solid $black;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -1px;
      border-radius: 0 0 .1875em .1875em;
      width: calc(100% + 2px);
      left: -1px;
      border: 1px solid $black;
      background: #FFF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.6875em;
      font-size: 1em;
      line-height: 157%;
      padding: .5em 1.25em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      border-bottom: 1px solid $black;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.filter-option,
.price-radio,
.project-item__input,
.switch-pair,
.m-switch,
.pay-way,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.75em;
    min-height: 1.25em;
    font-size: 1em;
    line-height: 120%;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: $border4;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
      color: $primary-secondary;
      &:hover{
        text-decoration-color: transparent;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: 100% auto;
      }
    }
    &:hover+label:before{
      border-color: $primary-secondary;
    }
    &:checked:hover+label:before{
      background-color: $primary-secondary;
    }
    &:disabled+label:before{
      border-color: $grey04;
      background-color: $grey02;
    }
    &:checked:disabled+label:before{
      background-color: $grey04;
    }
  }
  label {
    &:before {
      background-image: url(../img/check.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked{
    &~label{
      &:before{
        border: none;
        background-size: 100% auto;
      }
    }
  }
  input:hover{
    &~label{
      &:before{
        border-color: $primary-secondary;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='9.5' stroke='%230071bc'/%3E%3Ccircle cx='10' cy='10' r='7' fill='%230071bc'/%3E%3C/svg%3E");
      }
    }
  }
  input:disabled{
    &~label{
      color: $grey04;
      &:before{
        border-color: $grey03;
        background-color: $grey03;
      }
    }
    &:checked{
      &~label{
        &:before{
          background-color: transparent;
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='9.5' stroke='%23D3D3D3'/%3E%3Ccircle cx='10' cy='10' r='7' fill='%23D3D3D3'/%3E%3C/svg%3E");
        }
      }
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='9.5' stroke='%23303063'/%3E%3Ccircle cx='10' cy='10' r='7' fill='%23303063'/%3E%3C/svg%3E");
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.m-switch{
  display: flex;
  label{
    width: 2.5em;
    height: 1.5em;
    border-radius: 250em;
    background-color: $grey03;
    display: flex;
    align-items: center;
    padding: 0 .125em;
    transition: all .3s;
    &:before{
      content: '';
      transition: inherit;
      width: 1.25em;
      height: 1.25em;
      border-radius: 50%;
      background-color: $white;
    }
  }
  input:checked~label{
    background-color: $primary-secondary;
    &:before{
      transform: translateX(1em);
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


