.pay-sum{
  &__top{
    display: flex;
    align-items: center;
  }
  &__items{
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    margin-left: 1em;
    .price-radio{
      margin-left: 1.125em;
      &:first-child{
        margin-left: 0;
      }
    }
  }
  &__field{
    width: 16em;
    max-width: 100%;
    margin: 2em 0 0 auto;
    display: none;
  }
}

.price-radio{
  label{
    @include center;
    height: 2.75em;
    padding: 0 1.5em;
    border: $borderTer;
    border-radius: 6.25em;
    color: $primary;
  }
  input:checked~label{
    background-color: $primary-tertiary;
    border-color: $primary-tertiary;
    color: $primary;
  }
  &._other{
    label{
      border-color: $grey05;
      color: $grey05;
      padding: 0 .75em;
    }
  }
}

.requisite-ru{
  &__items{
    @include grid100gap(1.5em);
  }
}

.requisite-item{
  display: grid;
  grid-template-columns: 17.25em calc(100% - 18.25em);
  gap: 1em;
  text-transform: uppercase;
  &__title{
    font-size: .875em;
    line-height: 120%;
    color: $grey07;
  }
  &__desc{
    font-size: 1em;
    line-height: 120%;
  }
}

.contract-forms{
  &__items{
    @include grid100gap(1.5em);
  }
}

.doc-item{
  display: flex;
  align-items: center;
  padding: 1.5em;
  background-color: $primary-tertiary;
  border: $borderTer;
  &:hover{
    color: $black;
    border-color: $primary-secondary;
  }
  &:focus{
    background-color: transparent;
    border-color: $primary-secondary;
  }
  &:active{
    border-color: $primary;
  }
  &._disabled,
  &:disabled{
    border-color: $grey04;
    color: $grey05;
    background-color: transparent;
    .doc-item__icon{
      background-color: $grey02;
      path{
        fill: $grey05;
      }
    }
  }
  &__icon{
    width: 2.25em;
    height: 2.25em;
    border-radius: 50%;
    background-color: $primary;
    @include center;
    flex-shrink: 0;
    margin-right: .75em;
    svg{
      width: 1em;
    }
  }
}

.deposit-bottom{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 35em;
  max-width: 100%;
  margin: 1.5em auto 0;
  &__checks{
    @include grid100gap(.75em);
  }
  &__btn{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .pay-sum{
    &__top{
      display: block;
    }
    &__items{
      justify-content: unset;
      flex-wrap: wrap;
      margin: .5em 0 0;
      .price-radio{
        margin: 1.5em 1.5em 0 0;
      }
    }
    &__field{
      width: 100%;
      margin-top: 1.5em;
    }
  }

  .requisite-item{
    grid-template-columns: 100%;
  }

  .contract-forms{
    .deposit-group__content{
      padding: 0;
      border: none;
    }
    &__items{
      gap: .75em;
    }
  }

  .deposit-bottom{
    width: 100%;
    &__checks{
      width: 100%;
      gap: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }

  .doc-item{
    &._small{
      padding: 1em;
    }
  }
}


