.breadcrumbs {
    margin-bottom: 3em;
    li{
        position: relative;
        display: inline;
        color: $black;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 130%;
                font-weight: 500;
            }
        }
        &:last-child{
           pointer-events: none;
        }
    }
    a{
        font-size: 1em;
        line-height: 120%;
    }
}

.page-top{
    display: none;
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }

    .page-top{
        display: flex;
        padding: 1.5em 0;
        margin-top: -2em;
        &__back{
            display: flex;
            align-items: center;
            svg{
                width: 1.0625em;
                margin-right: .5em;
            }
        }
    }
}


