.founder{
  display: grid;
  grid-template-columns: 26em calc(100% - 27em);
  gap: 1em;
  margin-top: 2em;
  &__col{
    background-color: $primary;
    position: relative;
    min-height: 28.125em;
  }
  &__img{
    width: 11.7em;
    @include centerAbsolute;
  }
  &__col2{
    padding: 2.5em;
    border: $border;
    position: relative;
    display: flex;
    flex-direction: column;
    background-image: url(../img/lines.svg);
    background-position: right 2.5em bottom 2.5em;
    background-size: 16.6875em auto;
    background-repeat: no-repeat;
    &:after{
      content: '';
      position: absolute;
      width: 8.75em;
      height: 8.75em;
      background-color: #fff;
      right: 0;
      top: 0;
      transform: translate(50%,-50%) rotate(-45deg);
      border-left: $border;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
    max-width: 23.75em;
    @include grid100gap(1.125em);
  }
}

.slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      position: static;
      margin-top: .75em;
      @include center;
      &-bullet{
        width: .625em;
        height: .625em;
        margin: 0 1.25em !important;
        opacity: 1;
        background-color: $grey03;
        &-active{
          background-color: $button-main;
        }
      }
    }
  }
}

.senate{
  &__inner{
    position: relative;
  }
  @media screen and (min-width: 993px){
    &__top{
      position: absolute;
      width: calc(50% - .5em);
      left: 0;
      top: 0;
      height: 21.875em;
      border: $border;
      padding: 2em;
      background-image: url(../img/lines2.svg);
      background-repeat: no-repeat;
      background-position: left 2em bottom 2em;
      background-size: 31em auto;
    }
    .h2{
      max-width: 10em;
    }
  }
}

.senate-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1em;
        transform: unset !important;
        &:before{
          content: '';
          pointer-events: none;
        }
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.senate-card{
  min-height: 21.875em;
  height: 100%;
  padding: 2em;
  background-color: $primary-tertiary;
  display: flex;
  flex-direction: column;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 10.75em) 10em;
    gap: .75em;
    align-items: flex-start;
  }
  &__info{
    max-width: 21.25em;
    @include grid100gap(.75em);
  }
  &__img{
    @include imgCover(100%);
  }
  &__text{
    padding-top: .75em;
    margin-top: auto;
    max-width: 30.5em;
  }
}


@media screen and (max-width: 992px){
  .founder{
    margin-top: 1.5em;
    grid-template-columns: 20em calc(100% - 21em);
    &__col{
      min-height: 20em;
    }
    &__img{
      width: 9em;
    }
    &__col2{
      padding: 1em;
      background-image: unset;
      &:after{
        display: none;
      }
    }
    .h2{
      text-transform: unset;
    }
    .h3{
      font-size: 1.25em;
      text-transform: uppercase;
    }
  }

  .senate{
    overflow: hidden;
  }

  .senate-slider{
    margin-top: 2em;
    @include fullScreen2;
    .swiper{
      &-slide{
        width: 21em;
        padding-right: 1em;
      }
    }
  }

  .senate-card{
    padding: 1em;
    //min-height: 31.25em;
    &__top{
      grid-template-columns: 100%;
    }
    &__info{
      gap: .25em;
    }
    &__img{
      width: 8.75em;
      height: 8.75em;
      padding: 0;
    }
    .h3{
      font-size: 1.5em;
      font-weight: 400;
    }
    &__prof{
      @include truncate(3);
    }
    &__text{
      margin-top: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .founder{
    grid-template-columns: 100%;
  }
}
