}

.text{
  li,
  p{
    font-size: 1em;
    line-height: 120%;
  }
  ol,
  ul,
  p{
    @include lastM(0 0 1.25em);
  }
  b{
    font-weight: 700;
  }
  i{
    font-style: italic;
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
  }
  ol,ul{
    @include grid100gap(.5em);
    padding-left: .625em;
    li{
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ul{
    li{
      position: relative;
      padding-left: .9375em;
      &:before{
        content: '•';
      }
    }
  }
  ol{
    counter-reset: list;
    padding-left: .5em;
    li{
      counter-increment: list;
      position: relative;
      padding-left: 1em;
      &:before{
        content: counter(list) '. ';
      }
    }
  }
}

