.progress{
  padding-bottom: -.3125em;
  &__inner{
    padding: 4.5em 5em;
    background-color: $primary-tertiary;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.progress-item{
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .25em;
    img{
      width: 1.25em;
      margin-right: .75em;
    }
  }
}

.progress-bar2{
  margin-top: 3.5em;
  padding: 4.75em 0 .875em;
  border-left: 1px solid $black;
  border-right: 1px solid $black;
  border-bottom: 1px solid $black;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    width: .75em;
    height: 100%;
    background-color: $primary-tertiary;
    left: 0;
    bottom: 1px;
    z-index: 2;
  }
  &:after{
    left: unset;
    right: 0;
  }
  &__area{
    height: 6.875em;
    background-image: url(../img/progress2.svg);
    background-size: auto 110%;
    background-position: left -.125em bottom 0;
    position: relative;
    &-rect,
    &-fill,
    &-fill2{
      position: absolute;
      left: 0;
      bottom: 0;
      @media screen and (min-width: 577px){
        height: 100% !important;
        top: unset !important;
        bottom: 0;
      }
    }
    &-fill2{
      background-color: $primary-tertiary;
      bottom: 0;
      background-image: url(../img/progress3.svg);
      background-size: auto 110%;
      background-position: left -2em bottom 0;
      background-repeat: no-repeat;
      .progress-bar2__area-num{
        top: unset;
        bottom: 100%;
        right: 0;
        padding: 0 1em;
        margin-bottom: .625em;
        width: 100%;
      }
    }
    &-fill{
      background-color: $primary-secondary;
      height: calc(100% - .1875em);
      bottom: unset;
      top: 0;
    }
    &-rect{
      &:before,
      &:after{
        content: '';
        position: absolute;
        width: .75em;
        height: 100%;
        background-color: $primary-tertiary;
        right: 0;
        bottom: 0;
        z-index: 2;
      }
      &:after{
        right: unset;
        left: 100%;
        border-left: 1px solid $black;
      }
    }
    &-num{
      position: absolute;
      top: 0;
      right: .75em;
      opacity: .8;
    }
  }
}


@media screen and (max-width: 992px){
  .progress{
    background-color: $primary-tertiary;
    padding: 3em 0;
    &__inner{
      padding: 0;
    }
  }

  .progress-bar2{
    font-size: .75em;
  }

  .progress-item{
    font-size: .625em;
  }
}


@media screen and (max-width: 576px){
  .progress{
    &__inner{
      padding-top: 2.75em;
      position: relative;
    }
    &__top{
      flex-direction: column;
      align-items: flex-end;
      text-align: right;
      @include grid100gap(4.25em);
    }
    &__btn{
      margin-top: 1.5em;
      width: 100%;
    }
  }

  .progress-item{
    font-size: 1em;
    &__top{
      justify-content: flex-end;
      img{
        order: 2;
        margin: 0 0 0 .75em;
      }
    }
    .text24{
      font-size: 1em;
    }
    .h2{
      font-size: 1.75em;
    }
  }

  .progress-bar2{
    position: absolute;
    left: 0;
    top: 0;
    font-size: 1em;
    height: calc(100% - 5.625em);
    width: 5em;
    margin: 0;
    padding: 0 2em 0 .25em;
    border-top: 1px solid $black;
    border-right: none;
    &:before,
    &:after{
      height: .25em;
      width: 100%;
      bottom: 0;
    }
    &:before{
      bottom: unset;
      top: 0;
    }
    &__area,
    &__area-fill2{
      background-size: 110% auto;
      background-repeat: repeat-y;
    }
    &__area{
      background-image: url(../img/progress4.svg);
      height: 100%;
      width: 100%;
      &-rect,
      &-fill,
      &-fill2{
        width: 100% !important;

      }
      &-fill2{
        left: 0 !important;
        background-image: url(../img/progress5.svg);
        background-position: left -.125em top 0;
        .progress-bar2__area-num{
          bottom: 0;
          right: unset;
          left: 100%;
          width: 2.67em;
          padding: 0;
          color: $primary-secondary;
        }
      }
      &-rect{
        top: 0;
        bottom: unset;
        border-bottom: 1px solid $black;
        &:before,
        &:after{
          width: 100%;
          height: .25em;
          left: 0;
        }
        &:after{
          border-left: none;
          border-top: 1px solid $black;
          bottom: unset;
          top: 100%;
        }
      }
      &-num{
        top: 100%;
        right: 0;
        font-size: .75em;
        z-index: 3;
        text-align: right;
      }
    }
  }
}
