.header{
  position: relative;
  z-index: 5;
  padding: 1.25em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 7.125em;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__logo2{
    width: 6.6875em;
  }
  &__logo3{
    width: 7.25em;
    margin-right: 0;
  }
}

.header-menu{
  display: flex;
  flex-grow: 1;
  &__scroll{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.burger{
  display: none;
}

.nav{
  &>ul{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    &>li{
      margin-right: 2em;
      &:last-child{
        margin: 0;
      }
      a{
        display: flex;
        align-items: center;
        font-size: .875em;
        line-height: 120%;
        padding: .25em 0;
        border-top: 1px solid transparent;
        border-bottom: 1px solid transparent;
        &:hover{
          cursor: pointer;
          border-bottom-color: $primary;
        }
      }
      &.menu-item-has-children{
        position: relative;
        &>a{
          display: flex;
          align-items: center;
          &:hover{
            border-color: transparent;
          }
          &:after{
            content: '';
            width: .86em;
            height: 1em;
            background-image: url(../img/arrow.svg);
            @include bgImgSize(100%);
            flex-shrink: 0;
            margin-left: .26em;
            transition: all .3s;
          }
        }
      }
    }
  }
  .menu-item{
    display: flex;
  }
  .current-menu-item{
    &>a{
      border-bottom-color: $primary;
    }
  }
  @media screen and (min-width: 993px){
    .menu-item-has-children{
      &:hover{
        .sub-menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .sub-menu{
      margin-top: .75em;
      position: absolute;
      top: 100%;
      left: -1.25em;
      padding: 1em 1.5em;
      background-color: $white;
      border: 1px solid $grey03;
      white-space: nowrap;
      @include grid100gap(.5em);
      display: grid !important;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &:after{
        content: '';
        position: absolute;
        bottom: 100%;
        left: 0;
        width: 100%;
        height: .875em;
        margin-bottom: -1px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .5em 0;
    border-bottom: .5px solid $primary;
    .container{
      padding: 0 1.5em;
    }
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      width: 6.25em;
      z-index: 12;
      position: relative;
    }
    &__logos{
      display: none;
    }
    &._open{
      .header-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 12;
    width: 2.5em;
    height: 2.5em;
    position: relative;
    display: flex;
    img{
      width: 100%;
      display: block;
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
      width: 2.1875em;
    }
  }

  .header-menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 11;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: $white;
    height: $vh;
    padding-top: 4.125em;
    &__scroll{
      overflow-y: auto;
      height: 100%;
      border-top: 1px solid $primary;
      flex-direction: column;
      align-items: flex-start;
      padding: 3em 1.6875em;
    }
    &__btn{
      width: 100%;
      margin-top: 1.5em;
    }
  }

  .nav{
    &>ul{
      @include grid100gap(1.5em);
    }
    .menu-item{
      margin: 0;
      &.menu-item-has-children{
        display: block;
        &._open{
          a:after{
            transform: rotate(-180deg);
          }
        }
      }
    }
    .sub-menu{
      padding: .75em 1.5em;
      display: none;
      li{
        margin-bottom: 1em;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}


