.patrons{
  &__search{
    width: 27em;
    max-width: 100%;
    margin-top: 2em;
  }
}

.patrons-nav{
  margin-top: 2em;
  position: relative;
  z-index: 2;
  &__links{
    display: flex;
  }
  &__link{
    flex-shrink: 0;
    margin-right: 3.75em;
    height: 3.625em;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    font-weight: 700;
    color: $grey05;
    border-top: 2px solid transparent;
    border-bottom: 2px solid transparent;
    &:last-child{
      margin-right: 0;
    }
    span{
      font-size: 1.75em;
    }
    &._active{
      color: $primary-secondary;
      border-bottom-color: $primary-secondary;
    }
  }
}

.letters-filter{
  display: flex;
  padding: 2em 0;
  border-top: $border3;
  border-bottom: $border3;
  margin-top: -1px;
  &__link{
    font-size: 1.5em;
    height: 2em;
    padding: 0 .16em;
    display: flex;
    align-items: center;
    margin-right: .83em;
    color: rgba($black,.6);
    border-top: 2px solid transparent;
    border-bottom: 2px solid transparent;
    &._active{
      color: $primary-secondary;
      border-bottom-color: $primary-secondary;
    }
  }
}

.search-block{
  &._filled{
    .search-block__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .fg{
    input{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='18' height='19' viewBox='0 0 18 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.25 3.5C5.3505 3.5 3 5.8505 3 8.75C3 11.6495 5.3505 14 8.25 14C11.1495 14 13.5 11.6495 13.5 8.75C13.5 5.8505 11.1495 3.5 8.25 3.5ZM1.5 8.75C1.5 5.02208 4.52208 2 8.25 2C11.9779 2 15 5.02208 15 8.75C15 12.4779 11.9779 15.5 8.25 15.5C4.52208 15.5 1.5 12.4779 1.5 8.75Z' fill='%23EB1F42'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.957 12.4572C12.2499 12.1643 12.7247 12.1643 13.0176 12.4572L16.2801 15.7197C16.573 16.0126 16.573 16.4875 16.2801 16.7804C15.9872 17.0733 15.5124 17.0733 15.2195 16.7804L11.957 13.5179C11.6641 13.225 11.6641 12.7501 11.957 12.4572Z' fill='%23EB1F42'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-size: 1.125em auto;
      background-position: left .75em center;
      padding-left: 2.375em;
      &:active,
      &:focus{
        border-color: $button-pressed;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='18' height='19' viewBox='0 0 18 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.25 3.5C5.3505 3.5 3 5.8505 3 8.75C3 11.6495 5.3505 14 8.25 14C11.1495 14 13.5 11.6495 13.5 8.75C13.5 5.8505 11.1495 3.5 8.25 3.5ZM1.5 8.75C1.5 5.02208 4.52208 2 8.25 2C11.9779 2 15 5.02208 15 8.75C15 12.4779 11.9779 15.5 8.25 15.5C4.52208 15.5 1.5 12.4779 1.5 8.75Z' fill='%239d152c'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.957 12.4572C12.2499 12.1643 12.7247 12.1643 13.0176 12.4572L16.2801 15.7197C16.573 16.0126 16.573 16.4875 16.2801 16.7804C15.9872 17.0733 15.5124 17.0733 15.2195 16.7804L11.957 13.5179C11.6641 13.225 11.6641 12.7501 11.957 12.4572Z' fill='%239d152c'/%3E%3C/svg%3E");
      }
      &:hover{
        border-color: $button-focus;
      }
      &:disabled{
        background-color: $grey02;
        border-color: $grey02;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='18' height='19' viewBox='0 0 18 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.25 3.5C5.3505 3.5 3 5.8505 3 8.75C3 11.6495 5.3505 14 8.25 14C11.1495 14 13.5 11.6495 13.5 8.75C13.5 5.8505 11.1495 3.5 8.25 3.5ZM1.5 8.75C1.5 5.02208 4.52208 2 8.25 2C11.9779 2 15 5.02208 15 8.75C15 12.4779 11.9779 15.5 8.25 15.5C4.52208 15.5 1.5 12.4779 1.5 8.75Z' fill='%238f8f8e'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.957 12.4572C12.2499 12.1643 12.7247 12.1643 13.0176 12.4572L16.2801 15.7197C16.573 16.0126 16.573 16.4875 16.2801 16.7804C15.9872 17.0733 15.5124 17.0733 15.2195 16.7804L11.957 13.5179C11.6641 13.225 11.6641 12.7501 11.957 12.4572Z' fill='%238f8f8e'/%3E%3C/svg%3E");
      }
    }
  }
  &__clear{
    position: absolute;
    right: .75em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.125em;
    opacity: 0;
    pointer-events: none;
    svg{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .patrons-nav{
    margin-top: 1.5em;
    &__links{
      @include fullScroll;
    }
    &__link{
      height: 3.25em;
      margin-right: 1.5em;
      font-weight: 400;
      span{
        font-size: 1.5em;
      }
    }
  }

  .letters-filter{
    padding: .375em 0 .75em;
    flex-wrap: wrap;
    &__link{
      margin: .67em .41em 0 0;
      padding: 0 .3125em;
    }
  }
}


