.university{
  margin-bottom: -.3125em;
  padding: 3.125em 0;
  background-color: $primary-tertiary;
  &__grid{
    display: grid;
    grid-template-columns: 22.25em 53em;
    justify-content: space-between;
    gap: 1em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__col{
    display: flex;
    flex-direction: column;
    padding-bottom: .75em;
  }
  &__bottom{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: auto;
  }
  &__link{
    margin-top: 2em;
  }
  &__logo{
    width: 14.5em;
    max-width: 100%;
    img{
      max-width: 100%;
      display: block;
    }
  }
}

.process{
  padding: 19.75em 0 14.125em;
  &__grid{
    position: relative;
  }
  &__line,
  &__line2{
    width: 31.5em;
    left: 50%;
    transform: translateX(-50%);
  }
  &__line{
    bottom: 100%;
    margin-bottom: -.3125em;
    .m-line__num{
      left: 100%;
      margin-left: .25em;
      bottom: 1em;
    }
  }
  &__line2{
    top: 100%;
    margin-top: -.3125em;
    .m-line__num{
      right: 100%;
      top: 1em;
      margin-right: .5em;
    }
    .m-badge{
      left: 50%;
      bottom: -1.75em;
      transform: translateX(-50%);
      white-space: nowrap;
    }
  }
  &__line3{
    position: absolute;
    right: 7.75em;
    top: 100%;
    margin-top: -.3125em;
    width: 13.8125em;
    .m-badge{
      @include centerAbsolute;
    }
    .m-line__num{
      bottom: .625em;
      left: 100%;
      margin-left: .25em;
    }
  }
  &__line5{
    right: 7.75em;
    bottom: 100%;
    width: 20.9375em;
    .m-badge{
      top: 7.25em;
      left: 50%;
      transform: translateX(-50%);
    }
    .m-line__num{
      left: 100%;
      bottom: 1.5em;
      margin-left: .5em;
    }
  }
}

.process-card{
  padding: 3.125em;
  background-color: $primary-tertiary;
  &__icon{
    @include center;
    width: 4em;
    height: 4em;
    border-radius: 50%;
    background-color: $primary;
    margin-bottom: 1.125em;
    img{
      width: 1.5em;
    }
  }
  &__desc{
    border-top: $border;
    padding-top: 1.5em;
    margin-top: 1.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 100%;
      width: .3125em;
      height: .3125em;
      border-radius: 50%;
      transform: translate(-50%,50%);
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .university{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: 100%;
      position: relative;
      padding-top: 5.5em;
    }
    &__col{
      padding: 0;
    }
    &__col2{
      order: -1;
      @include fullScreen2;
    }
    &__link{
      width: 100%;
    }
    &__logo{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      img{
        max-width: 100%;
        max-height: 4.5em;
      }
    }
  }

  .process{
    padding: 11.625em 0 9em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__line{
      width: 1.625em;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      bottom: unset;
      margin: 0 0 0 -.3125em;
      .m-line__num{
        bottom: 100%;
        left: -.125em;
      }
    }
    .m-badge{
      width: calc(100vw - 3.375em);
      transform: translateY(-50%);
      left: 50%;
    }
    &__line2{
      margin: 0 auto -.3125em;
      position: relative;
      transform: unset;
      width: .8125em;
      z-index: 2;
      top: unset;
      left: unset;
      .m-line__num{
        top: unset;
        right: unset;
        left: 100%;
        bottom: 1em;
      }
      .m-badge{
        width: calc(100vw - 3.375em);
        height: 6.875em;
        bottom: 50%;
        transform: translate(-50%,50%);
        margin-bottom: .125em;
        white-space: unset;
        text-align: center;
      }
    }
    &__line3{
      width: 1.6875em;
      right: 100%;
      bottom: -5em;
      top: unset;
      margin: 0 -.625em 0 0;
      .m-badge{
        left: 0;
        margin-left: 1.0625em;
        top: 100%;
        margin-top: -.5em;
      }
      .process__line4{
        position: absolute;
        top: 100%;
        margin-top: 1em;
        width: .75em;
        left: 50vw;
        transform: translateX(calc(-50% - .625em));
      }
    }
    &__line5{
      width: 11em;
      right: 50%;
      transform: translateX(50%);
      .m-badge{
        @include centerAbsolute;
      }
    }
  }

  .process-card{
    &:nth-child(2){
      order: 2;
    }
  }
}


@media screen and (max-width: 576px){
  .process{
    &__line2{
      .m-badge{
        .text20{
          max-width: 10em;
        }
      }
    }
  }

  .process-card{
    padding: 1.5em;
    .h2{
      font-size: 1.75em;
      text-transform: unset;
    }
  }
}
