.switch-pair{
  flex-shrink: 0;
  padding: .25em;
  width: 18em;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
  border: $border;
  border-radius: 6.25em;
  color: rgba($black,.8);
  input{
    width: 50%;
    &:checked+label{
      color: $white;
    }
    &:nth-child(3){
      left: 50%;
      &:checked{
        &~.switch-pair__bg{
          left: calc(50% + .375em);
        }
      }
    }
  }
  label{
    height: 2.75em;
    @include center;
    transition: all .4s;
  }
  &__bg{
    position: absolute;
    z-index: -1;
    width: calc(50% - .625em);
    height: calc(100% - .5em);
    top: .25em;
    left: .25em;
    background-color: $primary;
    border-radius: 6.25em;
    transition: all .3s;
  }
}

.project-variants{
  @include grid100gap(1.5em);
  &__btn{
    display: none;
  }
}

.project-item{
  display: flex;
  align-items: center;
  &__input{
    label{
      display: flex;
      align-items: center;
      padding: .75em 1.5em;
      border: $borderTer;
      border-radius: 6.25em;
    }
    input:checked~label{
      background-color: $primary-tertiary;
    }
  }
  &__link{
    margin-left: .75em;
    height: auto;
  }
}

.fund-variants{
  .text{
    ul{
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .switch-pair{
    width: 100%;
  }

  .project-variants{
    gap: 2em;
    &:not(._show-all){
      .project-item:nth-child(2)~.project-item{
        display: none;
      }
    }
    &._show-all{
      .project-variants__btn{
        color: $primary;
        svg{
          transform: rotate(-90deg);
        }
        path{
          fill: $primary;
        }
      }
    }
    .project-item{
      &:nth-child(2){
        order: -1;
      }
      &:nth-child(3){
        margin-top: -1.25em;
      }
    }
    &__btn{
      display: flex;
      justify-content: space-between;
      height: 2.75em;
    }
  }

  .project-item{
    flex-direction: column;
    &__input{
      width: 100%;
      label{
        border-radius: 1.5em;
      }
    }
    &__link{
      margin: .75em 0 0;
    }
  }
}


