.footer{
  padding: 1.25em 0 1.5em;
  background-color: $primary;
  color: $white;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    width: 17.3125em;
    margin-left: -2.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col2{
    margin-left: 5.75em;
    padding-top: 1.875em;
    flex-grow: 1;
  }
  &__bottom{
    padding: 2em 0 0 12.75em;
    opacity: 0.8;
  }
}

.footer-contact{
  &__title{
    opacity: .6;
  }
  &__desc{
    display: flex;
    align-items: center;
    margin-top: .25em;
    img{
      width: 1.0625em;
      margin-right: .5em;
    }
    a{
      &:hover{
        color: $primary-secondary;
      }
    }
  }
}

.footer-socials{
  display: flex;
  align-items: center;
  &__item{
    margin-right: 2em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      max-height: 1.375em;
      max-width: 2.25em;
    }
    &:hover{
      path{
        fill: $primary-secondary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1em 0;
    &__inner{
      display: block;
    }
    &__logo{
      margin: 0 auto;
    }
    &__col2{
      margin: 0;
      padding-top: 2.25em;
    }
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__bottom{
      padding: .75em 0 0;
      text-align: center;
    }
  }

  .footer-contact{
    margin-bottom: .75em;
    &__title{
      text-align: center;
    }
  }

  .footer-socials{
    min-height: 4.4375em;
  }
}


