.page-block{
  padding: 2em 0 7.5em;
}

.last-block{
  padding-bottom: 7.5em;
}

.contacts{
  &__grid{
    padding: 5em 5.75em;
    background-color: $primary-tertiary;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 7.75em / 3));
    gap: 2em 3.875em;
  }
}

.contact-item{
  margin-bottom: 1.5em;
  &:last-child{
    margin-bottom: 0;
  }
  &__title{
    opacity: .6;
  }
  &__desc{
    display: flex;
    align-items: flex-start;
    margin-top: .5em;
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
  &__text{
    @include grid100gap(.25em);
    a{
      display: inline-flex;
      margin-right: auto;
    }
  }
  .socials{
    margin-top: .5em;
  }
}

.map{
  height: 30em;
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      padding: 1em;
      grid-template-columns: 100%;
    }
  }

  .contact-item{
    .socials{
      min-height: 4.375em;
    }
  }

  ._full-mob{
    @include fullScreen2;
  }
}


