.blog-card{
  padding: 1em;
  background-color: $primary-tertiary;
  border: 1px solid $primary-tertiary;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:focus{
    background-color: transparent;
    border-color: $primary-secondary;
  }
  &:hover{
    border-color: $primary-secondary;
  }
  &:active{
    border-color: $primary;
  }
  &__img{
    @include imgCover(71.35%);
  }
  &__date{
    margin: 1.125em 0 .5em;
  }
  &__desc{
    margin-top: .25em;
  }
  &__nav{
    margin-top: auto;
    padding: .5em 0 0;
    display: flex;
  }
}

.subscribe{
  padding-bottom: 7.5em;
  &__inner{
    padding: 8.125em 6.875em 6.375em;
    background-color: $primary-tertiary;
    clip-path: polygon(10em 0, 100% 0, 100% 100%, 0 100%, 0% 7em);
    background-image: url(../img/letter.svg), url(../img/lines.svg);
    background-repeat: no-repeat;
    background-position: right 14.5em top 3.5em, right 3.375em bottom 3.375em;
    background-size: 14.6875em auto, 16.6875em auto;
  }
  &__content{
    width: 100%;
    max-width: 28.5em;
    position: relative;
  }
  &__top{
    @include grid100gap(.75em)
  }
  &__hint{
    margin-top: 2em;
    color: $grey06;
    a{
      text-decoration: underline;
      color: $primary-secondary;
      &:hover{
        text-decoration-color: transparent;
      }
    }
  }
  &__thank{
    position: absolute;
    min-height: 100%;
    background-color: $primary-tertiary;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    pointer-events: unset;
    img{
      width: 2em;
      margin: 0 auto;
    }
    &-text{
      @include grid100gap(.75em);
      margin-top: .75em;
    }
  }
  &._success{
    .subscribe__thank{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.subscribe-form{
  margin-top: 2em;
  display: flex;
  align-items: center;
  .fg{
    flex-grow: 1;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: .75em;
  }
}

.news-item{
  &__desc{
    margin-top: .5em;
  }
  &__poster{
    margin: 2em auto 0;
    width: 68.5em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.article{
  padding: 5em 5.75em;
  background-color: $primary-tertiary;
  margin-top: 2em;
  color: rgba($black,.8);
}


@media screen and (max-width: 992px){
  .subscribe{
    padding-bottom: 0;
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.5em 1.75em;
      background-image: unset;
      clip-path: unset;
    }
    &__thank{
      text-align: left;
      align-items: flex-start;
      img{
        margin-left: 0;
      }
    }
  }

  .news-item{
    &__desc{
      margin-top: .5em;
    }
    &__poster{
      margin: 1.5em 0 0 -1.6875em;
      width: calc(100% + 3.375em);
      max-width: unset;
    }
  }

  .article{
    padding: 0;
    background-color: transparent;
  }

  .news-nav{
    &__prev,
    &__next{
      span{
        display: none;
      }
      svg{
        margin: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    &__thank{
      text-align: center;
      align-items: center;
      img{
        margin-left: auto;
      }
    }
  }

  .subscribe-form{
    display: block;
    &__btn{
      margin: .75em 0 0;
      width: 100%;
    }
  }
}
