.reports-filter{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.filter{
  margin-top: 2em;
  &__top{
    padding-bottom: .5em;
    border-bottom: $border3;
    margin-bottom: 1.125em;
  }
}

.filter-item{
  min-width: 12.5em;
  position: relative;
  &._open{
    .filter-item__show:after{
      transform: rotate(-180deg);
    }
    .filter-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.75em;
    border: 1px solid #a0a0a0;
    padding: 0 .75em;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      border-color: $primary;
    }
    &:after{
      content: '';
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      background-image: url(../img/arrow2.svg);
      @include bgImgSize(100%);
      margin-left: .375em;
      transition: all .3s;
    }
  }
  &__current{
    font-size: 1em;
    line-height: 120%;
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .5em;
    padding: .5em;
    background-color: $white;
    border: $border3;
    z-index: 3;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__options{
    @include grid100gap(.5em);
  }
  &._desktop-view{
    @media screen and (min-width: 993px){
      .filter-item__show{
        display: none;
      }
      .filter-item__hidden{
        opacity: 1;
        pointer-events: unset;
        position: static;
        margin: 0;
        padding: 0;
        border: none;
      }
      .filter-item__options{
        display: flex;
        flex-wrap: wrap;
        gap: unset;
        margin-top: -.75em;
      }
      .filter-option{
        margin: .75em .75em 0 0;
        &:last-child{
          margin-right: 0;
        }
        label{
          border: $borderTer;
          padding: 0 1.5em;
          border-radius: 6.25em;
        }
      }
      ._hide-desktop{
        display: none;
      }
      &._multi-select{
        .filter-option{
          margin-right: 4em;
          &:last-child{
            margin-right: 0;
          }
          label{
            height: unset;
            background-color: transparent !important;
            padding-right: 0;
            border: none;
            position: relative;
            cursor: pointer;
            display: block;
            padding-left: 1.75em;
            min-height: 1.25em;
            font-size: 1em;
            line-height: 120%;
            &:before,
            &:after{
              pointer-events: none;
            }
            &:before{
              content: '';
              position: absolute;
              z-index: 1;
              left: 0;
              top: 0;
              border: $border4;
              width: 1.25em;
              height: 1.25em;
              @include bgImgSize(0);
              transition: all .0s;
            }
          }
          input {
            &:checked+label {
              &::before {
                background-color: $primary;
                border-color: $primary;
                background-size: 100% auto;
              }
            }
            &:hover+label:before{
              border-color: $primary-secondary;
            }
            &:checked:hover+label:before{
              background-color: $primary-secondary;
            }
            &:disabled+label:before{
              border-color: $grey04;
              background-color: $grey02;
            }
            &:checked:disabled+label:before{
              background-color: $grey04;
            }
          }
          label {
            &:before {
              background-image: url(../img/check.svg);
            }
          }
        }
      }
    }
  }
}

.filter-option{
  label{
    padding: 0 .75em;
    display: flex;
    align-items: center;
    height: 2.75em;
    img{
      width: 1em;
      margin-right: .625em;
    }
  }
  input:checked{
    &~label{
      background-color: $primary-tertiary;
      color: $primary;
    }
  }
  input:disabled{
    &~label{
      color: $grey05;
    }
  }
}


@media screen and (max-width: 992px){
  .reports-filter{
    @include grid100gap(.75em);
  }

  .filter{
    margin-top: 1.5em;
    &__top{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .reports__items{
    gap: .75em;
  }
}
