@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.75em;
    height: 3.1875em;
    border-radius: 6.25em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
    svg{
        width: 1em;
        margin: 0 .25em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-red{
    color: $white;
    background-color: $button-main;
    border-color: $button-main;
    &:hover{
        color: $white;
        background-color: $button-hover;
        border-color: $button-hover;
    }
    &:active{
        color: $white;
        background-color: $button-pressed;
        border-color: $button-pressed;
    }
    &:focus{
        color: $white;
        background-color: $button-main;
        border-color: $button-focus         ;
    }
    &:disabled{
        color: $grey05;
        background-color: $grey02;
        border-color: $grey02         ;
    }
}

.m-btn-red-light{
    color: $button-main;
    background-color: $button-secondary;
    border-color: $button-secondary;
    &:hover{
        color: $button-hover;
    }
    &:active{
        color: $button-pressed;
    }
    &:focus{
        color: $button-main;
        border-color: $button-focus         ;
    }
    &:disabled{
        color: $grey05;
        background-color: $grey02;
        border-color: $grey02         ;
    }
}

.m-btn-text{
    color: $button-main;
    padding: 0;
    &:hover{
        color: $button-hover;
        path{
            fill: $button-hover;
        }
    }
    &:active{
        color: $button-pressed;
        path{
            fill: $button-pressed;
        }
    }
    &:disabled{
        color: $grey05;
        path{
            fill: $grey05;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.6875em;
    }
}


