@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

.opacity-60{
  opacity: .6;
}

.opacity-80{
  opacity: .8;
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    margin-right: 1em;
    @include center;
    &:last-child{
      margin-right: 0;
    }
    svg{
      max-width: 1.5625em;
      max-height: 1em;
    }
    &:hover{
      path{
        fill: $primary-secondary;
      }
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}


@media screen and (max-width: 992px){
  ._mt48-tablet{
    margin-top: 3em;
  }

  ._mt24-tablet{
    margin-top: 1.5em;
  }
}


