.senate-slider2{
  margin-top: 2em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
        gap: 1em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.senate-card2{
  height: 100%;
  padding: 2em;
  background-color: $primary-tertiary;
  &__img{
    width: 12.5em;
    height: 12.5em;
    margin-bottom: 3.75em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.75em);
  }
}


@media screen and (max-width: 992px){
  .senate-slider2{
    @include fullScreen2;
    .swiper-slide{
      width: 27em;
      padding-right: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .senate-slider2{
    .swiper{
      &-slide{
        width: 11.75em;
      }
    }
  }

  .senate-card2{
    padding: 1em;
    &__img{
      width: auto;
      height: auto;
      @include imgCover(100%);
      margin-bottom: 1.5em;
    }
    .h3{
      font-size: 1.5em;
      font-weight: 400;
    }
  }
}
