.project-item2{
  display: grid;
  grid-template-columns: 32.75em calc(100% - 33.75em);
  gap: 1.5em 1em;
  &__img{
    svg{
      width: 100%;
    }
  }
  .fundraising-progress{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.project-info{
  display: grid;
  grid-template-columns: calc(100% - 33.75em) 32.75em;
  gap: .75em 1em;
  margin-top: 3em;
  &__text{
    padding: 2em;
    background-color: $primary-tertiary;
    color: rgba($black,.8);
    p{
      @include lastM(0 0 2em);
    }
  }
}

.project-slider{
  &__img{
    svg{
      width: 100%;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      position: static;
      margin: 0;
      width: 3em;
      height: 3em;
      svg{
        width: 1em;
      }
      &.swiper-button-disabled{
        path{
          fill: $grey05;
        }
      }
    }
    &-pagination{
      position: static;
      width: auto;
      margin: 0 1em;
      font-size: 1em;
      line-height: 120%;
      color: $grey07;
      letter-spacing: -.1em;
    }
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: .5em;
  }
}


@media screen and (max-width: 992px){
  .project-item2{
    grid-template-columns: 23em calc(100% - 24em);
  }

  .project-info{
    grid-template-columns: calc(100% - 24em) 23em;
    &__text{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .project-item2{
    grid-template-columns: 100%;
    &__img{
      @include fullScreen2;
    }
  }

  .project-info{
    grid-template-columns: 100%;
    &__col2{
      order: -1;
      @include fullScreen2;
    }
  }

  .project-slider{
    &__nav{
      justify-content: center;
    }
  }
}
