.news-collection{
  padding-bottom: 8.75em;
  position: relative;
  z-index: 3;
  &__top{
    display: flex;
    justify-content: center;
  }
  &__title{
    position: relative;
    .m-line{
      left: 100%;
      margin-left: 1.5em;
      bottom: 1em;
      width: 19.5em;
    }
  }
  &__bottom{
    margin-top: 3.75em;
  }
  &__nav{
    position: relative;
    .m-line{
      top: 1.3125em;
      right: 100%;
      margin-right: -.3125em;
      width: 30.125em;
    }
  }
}

.news-slider{
  margin-top: 2.5em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
        gap: 1em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.m-line{
  position: absolute;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
  .m-badge,
  &__num{
    position: absolute;
  }
}

.finance-directions{
  padding-top: 14.75em;
  padding-bottom: 12em;
  position: relative;
  z-index: 2;
  &__top{
    display: flex;
    justify-content: center;
  }
  &__title{
    position: relative;
    z-index: 2;
    .m-line1{
      left: -13.5em;
      bottom: .75em;
      width: 38.75em;
      .m-line__num{
        position: absolute;
        left: 9em;
        bottom: 1em;
      }
    }
    .m-line2{
      left: 100%;
      margin-left: 2.75em;
      top: 1em;
      width: 11em;
    }
    .m-badge{
      position: absolute;
      left: 11.75em;
      bottom: 7.5em;
    }
  }
  &__grid{
    margin-top: 3.25em;
    display: grid;
    grid-template-columns: 46.25em;
    position: relative;
  }
}

.finance-item{
  padding: 1.5em;
  transition: all .3s;
  &._active,
  &:hover{
    cursor: pointer;
    background-color: $primary-tertiary;
    .finance-item__icon{
      background-color: $white;
    }
  }
  &._active{
    .finance-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: flex-start;
  }
  &__icon{
    width: 4em;
    height: 4em;
    @include center;
    flex-shrink: 0;
    background-color: $primary-tertiary;
    transition: all .3s;
    border-radius: 50%;
    margin-right: 1.125em;
    img{
      width: 1.5em;
    }
  }
  &__content{
    max-width: 30.3125em;
  }
  &__desc{
    margin-top: .25em;
    max-width: 28.125em;
  }
  &__hidden{
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    position: absolute;
    margin-right: 1px;
    right: 0;
    top: 0;
    width: calc(100% - 46.25em);
    padding: 3.75em 3.5em;
    background-color: $primary-tertiary;
    height: 45em;
    overflow: hidden;
    @media screen and (min-width: 993px){
      min-height: 100%;
      display: block !important;
    }
  }
  &__list{
    @include grid100gap(1.5em);
    margin-top: 2em;
  }
  &__project{
    position: relative;
    border-top: $border;
    padding-top: 1.5em;
    @include grid100gap(.5em);
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 100%;
      width: .3125em;
      height: .3125em;
      border-radius: 50%;
      transform: translate(-50%,50%);
      background-color: $primary;
    }
  }
  &__nav{
    display: none;
  }
}

.m-badge{
  padding: 0 1.5em;
  height: 3.625em;
  background-color: $primary;
  text-transform: uppercase;
  color: $white ;
  font-weight: 700;
  @include center;
  img{
    width: 1.625em;
  }
}


@media screen and (max-width: 992px){
  .m-line{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .news-collection{
    padding-bottom: 4em;
    &__top{
      justify-content: unset;
      position: relative;
    }
    &__title{
      position: static;
      .m-line{
        right: .25em;
        left: unset;
        bottom: 100%;
        margin-bottom: -1em;
        width: 7.75em;
      }
    }
    &__bottom{
      display: none;
    }
  }

  .news-slider{
    @include fullScreen2;
    margin-top: 1.5em;
    overflow: hidden;
    .swiper{
      &-slide{
        width: 19.75em;
        padding-right: 1em;
      }
    }
  }

  .finance-directions{
    padding-bottom: 7.5em;
    padding-top: 11.625em;
    &__top{
      justify-content: unset;
      position: relative;
    }
    &__title{
      position: static;
      .m-line1{
        left: 50%;
        transform: translateX(-50%);
        bottom: 100%;
        margin-bottom: .125em;
        width: 9.1875em;
        .m-line__num{
          left: 100%;
          margin-left: .25em;
        }
      }
      .m-line2{
        display: none;
      }
      .m-badge{
        width: calc(100vw - 3.375em);
        transform: translateX(-50%);
        left: 50%;
        bottom: 3.5em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
    ._hide-tablet{
      display: none;
    }
    &__items{
      @include grid100gap(1.5em)
    }
  }

  .finance-item{
    padding: 1em;
    background-color: $primary-tertiary;
    &._active2{
      .finance-item__show{
        &:after{
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.79289 5.96208C9.18342 5.57156 9.81658 5.57156 10.2071 5.96208L16.2071 11.9621C16.5976 12.3526 16.5976 12.9858 16.2071 13.3763L10.2071 19.3763C9.81658 19.7668 9.18342 19.7668 8.79289 19.3763C8.40237 18.9858 8.40237 18.3526 8.79289 17.9621L14.0858 12.6692L8.79289 7.3763C8.40237 6.98577 8.40237 6.35261 8.79289 5.96208Z' fill='%23303063'/%3E%3C/svg%3E");
          transform: rotate(-90deg);
        }
      }
      .finance-item__title{
        color: $primary;
      }
    }
    &__show{
      align-items: center;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1.5625em;
        height: 1.5625em;
        margin-left: auto;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.79289 5.96208C9.18342 5.57156 9.81658 5.57156 10.2071 5.96208L16.2071 11.9621C16.5976 12.3526 16.5976 12.9858 16.2071 13.3763L10.2071 19.3763C9.81658 19.7668 9.18342 19.7668 8.79289 19.3763C8.40237 18.9858 8.40237 18.3526 8.79289 17.9621L14.0858 12.6692L8.79289 7.3763C8.40237 6.98577 8.40237 6.35261 8.79289 5.96208Z' fill='black'/%3E%3C/svg%3E");
        @include bgImgSize(100%);
        transition: all .3s;
      }
    }
    &__icon{
      margin-right: .75em;
      width: 1.5625em;
      height: auto;
      background-color: transparent !important;
      img{
        width: 100%;
      }
    }
    &__title{
      font-size: 1.25em;
      text-transform: uppercase;
    }
    &__desc{
      display: none;
    }
    &__hidden{
      padding: .75em 0 0;
      opacity: 1;
      pointer-events: unset;
      transition: unset;
      position: static;
      background-color: transparent;
      width: 100%;
      height: unset;
      display: none;
      .h3{
        display: none;
      }
    }
    &__list{
      margin: 0;
      gap: 0;
    }
    &__project{
      padding: 0 0 0 .625em;
      border: none;
      &:before{
        display: none;
      }
      &-title{
        display: none;
      }
      &-desc{
        opacity: 1;
        padding-left: 1em;
        position: relative;
        &:before{
          content: '•';
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
    &__nav{
      display: flex;
      justify-content: flex-end;
      margin-top: .75em;
    }
  }
}


