.patrons2{
  padding: 7em 0 18.25em;
  &__grid{
    display: grid;
    grid-template-columns: 26.875em 39.5em;
    justify-content: space-between;
    gap: 3em;
    position: relative;
  }
  &__desc{
    margin-top: 1.125em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  .m-line{
    width: 47.125em;
    position: absolute;
    right: 13em;
    top: 100%;
    &__num{
      right: 5.25em;
      top: 4em;
    }
  }
  .m-badge{
    bottom: 6.75em;
    left: 12.625em;
  }
}

.main-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-pagination{
      display: none;
    }
    &-button{
      width: 3.5em;
      height: 3.5em;
      background-color: $white;
      border-radius: 50%;
      @include center;
      top: 50%;
      transform: translateY(-50%);
      &:hover{
        background-color: $primary;
        path{
          fill: $white;
        }
      }
      svg{
        width: 1.5em;
      }
      &-prev{
        left: .875em;
      }
      &-next{
        right: .875em;
      }
    }
  }
}

.main-slide{
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      object-fit: cover;
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &__inner{
    min-height: 40em;
    padding: 13.75em 0 9em 6.75em;
  }
  &__content{
    max-width: 100%;
    width: 40.5em;
    padding: 3.125em;
    backdrop-filter: blur(60px);
    background: rgba(27, 86, 191, 0.5);
    color: $white;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: .75em;
  }
}


@media screen and (max-width: 992px){
  .patrons2{
    position: relative;
    padding: 4.375em 0 11.625em;
    &__grid{
      grid-template-columns: 20em calc(100% - 23em);
    }
    &__items{
      width: 12.5em;
      max-width: 100%;
    }
    .patron-card{
      padding: 1em;
      &__type,
      &__desc,
      &__img-wrap{
        margin-top: 1em;
      }
    }
    .m-line{
      width: 5.4375em;
      right: 2.4375em;
      bottom: -4em;
      top: unset;
      &__num{
        right: unset;
        left: 100%;
        top: 11.875em;
        margin-left: .5em;
      }
    }
    .m-line2{
      right: 50%;
      bottom: 0;
      transform: translateX(50%);
      width: .75em;
    }
    .m-badge{
      width: calc(100vw - 3.375em);
      right: -2.4375em;
      left: unset;
      bottom: unset;
      top: 100%;
    }
  }

  .main-slider{
    .swiper{
      &-pagination{
        display: flex;
      }
      &-button{
        display: none;
      }
    }
  }

  .main-slide{
    &__inner{
      padding-left: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .patrons2{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .main-slide{
    height: auto;
    display: flex;
    flex-direction: column;
    .container{
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      padding: 0;
    }
    &__inner{
      flex-grow: 1;
      padding: 0;
      display: flex;
      flex-direction: column;
      min-height: unset;
    }
    &__img{
      @include imgCover(68.8%);
    }
    &__content{
      padding: 1.5em 1.6875em 2.75em;
      flex-grow: 1;
    }
    .h1{
      font-size: 1.75em;
      text-transform: unset;
    }
  }
}
