.projects{
  &__subtitle{
    margin-top: 2em;
    padding-bottom: .5em;
    border-bottom: $border3;
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
}

.project-card{
  padding: 1em;
  border: $borderTer;
  background-color: $primary-tertiary;
  display: grid;
  grid-template-columns: 17.6875em calc(100% - 17.6875em - 1.125em);
  gap: 1.125em;
  transition: all .3s;
  &:hover{
    border-color: $primary-secondary;
  }
  &:active{
    border-color: $primary;
  }
  &:focus{
    border-color: $primary-secondary;
    background-color: transparent;
  }
  &._disabled{
    pointer-events: none;
    color: $grey05;
    border-color: $grey04;
    background-color: transparent;
    .project-card__tag{
      border-color: $grey05;
      color: $grey05;
      path{
        stroke: $grey05;
      }
    }
    .progress-bar{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='3438' height='13' viewBox='0 0 3438 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.00019933 8.79236L3437.2 4.04071' stroke='%238f8f8e' stroke-width='8' stroke-linejoin='round' stroke-dasharray='6 6'/%3E%3C/svg%3E");
      &__fill{
        background-color: $grey05;
      }
    }
    .gray-text7{
      color: $grey05;
    }
    .project-card__btn{
      background-color: $grey02;
      border-color: $grey02;
      color: $grey05;
    }
    .project-card__status{
      color: $grey05;
      path{
        fill: $grey05;
      }
    }
  }
  &__img{
    @include imgCover(73.2%);
  }
  &__col2{
    display: flex;
  }
  &__content{
    flex-grow: 1;
  }
  &__nav{
    flex-shrink: 0;
    align-self: center;
    margin-left: 1.125em;
  }
  &__tag{
    display: inline-flex;
    align-items: center;
    padding: 0 .75em;
    height: 1.75em;
    border-radius: 6.25em;
    border: $border;
    color: $primary;
    margin-bottom: .75em;
    svg{
      width: 1em;
      margin-right: .625em;
    }
  }
  &__desc{
    margin-top: .75em;
  }
  .fundraising-progress{
    margin-top: 1.5em;
  }
  &__status{
    margin-top: .75em;
    display: flex;
    align-items: center;
    color: $input-success;
    font-weight: 700;
    text-transform: uppercase;
    svg{
      width: 1.625em;
      margin-right: .5em;
    }
  }
}

.fundraising-progress{
  &__nums{
    margin-top: .25em;
    display: flex;
    justify-content: flex-end;
    position: relative;
  }
  &__current{
    position: absolute;
    top: 0;
    transform: translateX(-50%);
  }
}

.progress-bar{
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='3438' height='13' viewBox='0 0 3438 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.00019933 8.79236L3437.2 4.04071' stroke='%230071BC' stroke-width='8' stroke-linejoin='round' stroke-dasharray='6 6'/%3E%3C/svg%3E");
  height: .5em;
  background-size: auto .8125em;
  background-position: left bottom;
  background-repeat: repeat-x;
  &__fill{
    display: block;
    height: 100%;
    background-color: $primary;
  }
}


@media screen and (max-width: 992px){
  .projects{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.5em 1em;
    }
  }

  .project-card{
    display: block;
    &__col2{
      display: block;
      margin-top: 1.125em;
    }
    &__nav{
      margin: 1.125em 0 0;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .projects{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
