.manage-capital{
  &__items{
    @include grid100gap(2em);
    margin-top: 2em;
    max-width: 59.75em;
  }
}

.manage-card{
  @include grid100gap(.75em);
  &__top{
    padding: 1.5em;
    background-color: $primary-tertiary;
  }
  &__bottom{
    padding: 2em;
    border: $border3;
    display: grid;
    grid-template-columns: 12.125em calc(100% - 13.625em);
    gap: 1.5em;
  }
  &__img{
    width: 100%;
    display: block;
    max-width: 12.125em;
  }
  &__nav{
    display: flex;
    margin-top: .5em;
  }
  &__link{
    color: $primary-secondary;
    text-decoration: underline;
  }
  &__text{
    margin-top: 1.125em;
    p{
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 992px){
  .manage-capital{
    &__items{
      margin-top: 1.5em;
    }
  }

  .manage-card{
    &__bottom{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .manage-card{
    &__bottom{
      grid-template-columns: 100%;
    }
  }
}
