.payment-group{
  padding: 2em;
  border: $border2;
  display: grid;
  grid-template-columns: 24em calc(100% - 25em);
  gap: .75em 1em;
}

.pay-target{
  &__project{
    padding: .75em 1.5em;
    border-radius: 6.25em;
    background-color: $primary-tertiary;
    color: $primary;
  }
  .text{
    p{
      @include lastM(0 0 .5em);
    }
  }
}

.card-form{
  max-width: 35.25em;
  width: 100%;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.625em));
    gap: 2em 3.25em;
  }
  .fg{
    .hint-block{
      position: absolute;
      right: .75em;
      bottom: .875em;
    }
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__hint{
    a{
      text-decoration: underline;
    }
  }
  &__methods{
    margin-top: 3em;
    display: flex;
    align-items: center;
    &-item{
      margin-right: 2em;
      max-width: 6.5em;
      max-height: 2.5em;
    }
  }
}

.hint-block{
  position: relative;
  &:hover{
    .hint-block__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    width: 1em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      cursor: pointer;
      path{
        fill: $black;
      }
    }
  }
  &__hidden{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    margin-top: -1px;
    left: -.5em;
    padding-top: .375em;
  }
  &__inner{
    padding: 1em;
    width: 20em;
    background-color: $primary-tertiary;
    display: flex;
    align-items: center;
    box-shadow: .125em .125em .5em rgba(180, 185, 198, 0.6);
    color: $primary;
    img{
      margin-right: .5em;
      width: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .payment-group{
    grid-template-columns: 100%;
    padding: 0;
    border: none;
  }

  .pay-target{
    &__project{
      border-radius: 1.5em;
    }
  }

  .card-data{
    .payment-group__col2{
      padding: 1em;
      margin-top: .5em;
    }
  }

  .card-form{
    max-width: 100%;
    .hint-block{
      &__hidden{
        left: unset;
        right: -.75em;
        padding-top: 1.125em;
      }
    }
    &__btn{
      width: calc(50% - 1.625em);
    }
    &__methods{
      margin-top: 1.25em;
      justify-content: center;
      flex-wrap: wrap;
      &-item{
        margin: .75em .375em 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .card-form{
    &__fields{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    .hint-block{
      &__inner{
        width: 18.125em;
      }
    }
    &__nav{
      flex-direction: column;
    }
    &__btn{
      width: 100%;
      order: -1;
      margin: 0 0 .75em;
    }
  }
}
